import React, { Component } from 'react';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import { CircularProgress, Snackbar, Divider, Select, MenuItem, InputLabel, FormControl } from '@material-ui/core';
import axios from 'axios';
import formData from './sparkConfig.json';
import styled from 'styled-components';


const ImgTag = styled.img`
  height: 100%;
  max-width: 100%;
  max-height: 100%;
  margin-left: auto;
`;

class SparkForm extends Component{
  constructor(props){
    super(props);
    this.state={
      submitLoading: false,
      errorMessage: '',
      error: false,
      submitSuccess: false,
      submitInfo:{},
      formError:{
        name: '',
        email: '',
        mobile: '',
        institute: '',
        city: '',
        state: '',
        zip: ''
      },
      values:{
        name: '',
        email: '',
        size: 'L',
        mobile: '',
        hostel: 'Hostel 7',
        city: '',
        state: '',
        zip: ''
      }
    };
  }

  onFormChange(val, field){
    
    this.setState(prevState =>{
      return ({
        ...prevState,
          values:{
            ...prevState.values,
            [field]: val
        },
        formError:{
          ...prevState.formError,
          [field]: ''
        },
      });
    });
  }

  handleSubmission(){
    this.setState({
      submitLoading: true
    });
    axios({
      method: 'POST',
      url: '/payment/spark/pay',
      data: {
        ...this.state.values,
        amount: 35000,
      }
    }).then((response)=>{
      console.log(response);
      this.setState({
        submitInfo:{
          ...response.data
        },
        submitLoading: false,
        submitSuccess: true
      }, ()=>{
        const {submitInfo, values}= this.state;
        const script = document.createElement("script");
        script.onload=function(){
          document.getElementsByClassName('razorpay-payment-button')[0].style.display='none';
          document.getElementsByClassName('razorpay-payment-button')[0].click();
        }
        const attributes={
          'data-key': submitInfo.key,
          'data-amount': "35000" ,
          'data-currency':"INR",
          'data-order_id': submitInfo.orderID,
          'data-buttontext': "Continue to Payment",
          'data-name': "Tecnoesis 2020",
          'data-description': "Tecnoesis 2020 NIT Silchar",
          'data-prefill.name' :values.name,
          'data-prefill.email': values.email,
          'data-prefill.contact': values.mobile
        };
        script.src = "https://checkout.razorpay.com/v1/checkout.js";

        for(var key in attributes){
          script.setAttribute(key, attributes[key]);
        }
        script.async = true;
        document.forms['reg'].appendChild(script);
      });
    }).catch((e)=>{
      console.log(e.response);
      if(e.response.data.error==='email'){
        this.setState({
          submitLoading: false,
          error: true,
          formError:{
            email: 'Email Already Registered'
          },
          errorMessage: 'Email Already Registred'
        });
      }
      else{
        this.setState({
          submitLoading: false,
          error: true,
          errorMessage: 'Error Occurred'
        });
      }
    });
  }

  onTextBlur(field){
    if(field==='email'){
      if(!/(?:[a-z0-9!#$%&'*+/=?^_`{|}~-]+(?:\.[a-z0-9!#$%&'*+/=?^_`{|}~-]+)*|"(?:[\x01-\x08\x0b\x0c\x0e-\x1f\x21\x23-\x5b\x5d-\x7f]|\\[\x01-\x09\x0b\x0c\x0e-\x7f])*")@(?:(?:[a-z0-9](?:[a-z0-9-]*[a-z0-9])?\.)+[a-z0-9](?:[a-z0-9-]*[a-z0-9])?|\[(?:(?:(2(5[0-5]|[0-4][0-9])|1[0-9][0-9]|[1-9]?[0-9]))\.){3}(?:(2(5[0-5]|[0-4][0-9])|1[0-9][0-9]|[1-9]?[0-9])|[a-z0-9-]*[a-z0-9]:(?:[\x01-\x08\x0b\x0c\x0e-\x1f\x21-\x5a\x53-\x7f]|\\[\x01-\x09\x0b\x0c\x0e-\x7f])+)\])/.test(this.state.values.email)){
        this.setState({
          formError:{
            email: 'Invalid Email'
          }
        })
      }
    }
    else if(field==='mobile'){
      if(this.state.values.mobile.length!==10){
        this.setState({
          formError:{
            mobile: 'Invalid Mobile Number. Must be of 10 digits.'
          }
        })
      }
    }
  }

  render(){
    const { values, submitSuccess, formError } =this.state;
    const isDisabled = values.name==='' || values.email==='' || values.mobile==='' || (formError.email && !formError.email.length) || (formError.mobile && !formError.mobile.length);

    return (
      <Grid container spacing={0} >
          <Grid item sm={12} md={6} xs={12} style={{ backgroundColor: '#000', color: '#fff', padding: '0', minHeight: '85vh', textAlign: 'center', maxHeight: '90vh'}} >
            <ImgTag src="/misc/sparkTee.jpg" alt="Spark Registration"
            />
          </Grid>
          <Grid item sm={12} md={6} xs={12} style={{ padding: '2vh 2vw', color: '#000' }} >

            <Typography component="h1" variant="h4" align="center">
              Tecnoesis 2020

            </Typography>
      <React.Fragment>
        <Grid item xs={12} sm={12} style={{margin: '5% 0%', textAlign: 'center', fontSize: '2.5vh'}}>
          Spark T-Shirt
        </Grid>
        
        <Grid container spacing={3}>
          {formData.map((form)=>{
            if(form.type==='information'){
              return(
                <Grid item xs={12} sm={12} key={form.name}>
                  <Typography variant="h6" gutterBottom align="left">
                    {form.text}
                  </Typography>
                </Grid>
              );
            }
            return(
              <Grid item xs={form.grid.xs} sm={form.grid.sm} key={form.name}>
                <TextField
                  required={form.required}
                  name={form.name}
                  label={form.label}
                  error={formError[form.name] && formError[form.name].length>0}
                  helperText={formError[form.name]}
                  value={values[form.name] || ''}
                  fullWidth={form.fullWidth}
                  onChange={(event)=>{
                    this.onFormChange(event.target.value, form.name);
                  }}
                  onBlur={(event)=>{
                    this.onTextBlur(form.name);
                  }}
                />
              </Grid>
            )
          })}
        </Grid>

        <Grid item xs={12} sm={6} style={{ margin: '3vh 0' }}>
          <FormControl style={{ minWidth: '70%' }}>
          <InputLabel id="demo-simple-select-outlined-label">
            Hostel
          </InputLabel>
          <Select
            labelId="demo-simple-select-outlined-label"
            id="demo-simple-select-outlined"
            value={values.hostel}
            onChange={(event)=>{
              this.onFormChange(event.target.value, 'hostel');
            }}
            labelWidth={100}
          >
            <MenuItem value="Hostel GH1 A">Hostel GH1 A</MenuItem>
            <MenuItem value="Hostel GH1 B">Hostel GH1 B</MenuItem>
            <MenuItem value="Hostel GH2">Hostel GH2</MenuItem>
            <MenuItem value=">Hostel GH3">Hostel GH3</MenuItem>
            <MenuItem value="Aryabhatta Hostel">Aryabhatta Hostel</MenuItem>
            <MenuItem value="Hostel 3">Hostel 3</MenuItem>
            <MenuItem value="Hostel 4">Hostel 4</MenuItem>
            <MenuItem value="Hostel 5">Hostel 5</MenuItem>
            <MenuItem value="Hostel 6">Hostel 6</MenuItem>
            <MenuItem value="Hostel 7">Hostel 7</MenuItem>
            <MenuItem value="Hostel 8">Hostel 8</MenuItem>
            <MenuItem value="Hostel 9A">Hostel 9A</MenuItem>
            <MenuItem value="Hostel 9B">Hostel 9B</MenuItem>
            <MenuItem value="Hostel 9D">Hostel 9D</MenuItem>
            
            <MenuItem value="Other">Other</MenuItem>
            
          </Select>
        </FormControl>
        </Grid>



        <Divider variant="middle" style={{ margin: '2vh 0' }} />
        <Grid item xs={12} sm={12} style={{ marginBottom: '3vh' }}>
          <FormControl style={{ minWidth: '30%' }}>
          <InputLabel id="demo-simple-select-outlined-label">
            Size
          </InputLabel>
          <Select
            labelId="demo-simple-select-outlined-label"
            id="demo-simple-select-outlined"
            value={values.size}
            onChange={(event)=>{
              this.onFormChange(event.target.value, 'size');
            }}
            labelWidth={100}
          >
            <MenuItem value="S">S</MenuItem>
            <MenuItem value="M">M</MenuItem>
            <MenuItem value="L">L</MenuItem>
            <MenuItem value="XL">XL</MenuItem>
            <MenuItem value="XXL">XXL</MenuItem>
            
          </Select>
        </FormControl>
        </Grid>

        <Grid item xs={12} sm={6}>
          <TextField
            label="T-Shirt Cost"
            defaultValue="350"
            disabled
          />
        </Grid>
    
        <div style={{ marginTop: '4vh', textAlign: 'center' }}>
          <Button
            variant="contained"
            disabled={isDisabled || this.state.submitLoading || this.state.submitSuccess}
            style={{ backgroundColor: '#000', color: '#fff', marginRight: '20px' }}
            onClick={()=>{
              this.handleSubmission();
            }}
          >
            {this.state.submitLoading && (<CircularProgress style={{ color: "#fff" }} />)}
            {!this.state.submitLoading && ("Continue To Payment")}
          </Button>

        </div>
        {submitSuccess && (
          <form action="/payment/success/" method="POST" name="reg">
            <input type="hidden" custom="Hidden Element" name="hidden" />
          </form>
        )}
        <Snackbar
          open={this.state.error}
          anchorOrigin={{ horizontal: 'right', vertical: 'top' }}
          message={this.state.errorMessage}
          variant="error"
          onClose={()=> {
            this.setState({
              error: false
            })
          }}
          autoHideDuration={5000}
        />
        </React.Fragment>
      </Grid>
    </Grid>
    );
  }
}

export default SparkForm;
